import {mapActions} from 'vuex'
export default {
	name: 'uni-number-box',
	props: {
		_id:{
			type:[Number, String],
			required: true
		},
		count: {
			type: Number,
			required:true,
			default: 1
		},
		min: {
			type: Number,
			default: 1
		},
		max: {
			type: Number,
			default: 100
		},
		step: {
			type: Number,
			default: 1
		},
		disabled: {
			type: Boolean,
			default: true
		}
	},
	data() {
		return {
			
		}
	},
	computed: {
		inputValue(){
			return this.count
		},
		disableSubtract() {
			return this.inputValue <= this.min
		},
		disableAdd() {
			return this.inputValue >= this.max
		}
	},

	methods: {
		...mapActions( 'cart/', ['incrementCartItemCount', 'decrementCartItemCount']),
		
		_calcValue(type) {
			if (this.disabled) {
				return
			}
			const scale = this._getDecimalScale()
			let value = this.inputValue * scale
			let step = this.step * scale
			if (type === 'subtract') {
				value -= step
			} else if (type === 'add') {
				value += step
			}
			if (value < this.min || value > this.max) {
				return
			}
			this.inputValue = value / scale;
		},

		_onBlur(event) {
			let value = event.detail.value
			if (!value) {
				this.inputValue = 0
				return
			}
			value = +value;
			if (value > this.max) {
				value = this.max
			} else if (value < this.min) {
				value = this.min
			}
			this.inputValue = value
		}
	}
}
